import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function MemberAssignManagerList({ refresh }) {
  const { memberAssignManagers } = useSelector(
    (state) => state.memberAssignManager
  );

  const options = useRef({
    columns: columnsOptions(),
  });

  return <SpreadSheet options={options} data={memberAssignManagers} />;
}
