export const memberAssignManagerOptionsFactory = (
  memberAssignManagers,
  available = "true"
) => {
  return memberAssignManagers
    .filter((assignManager) => assignManager.available === available)
    .map((assignManager) => ({
      key: assignManager.id,
      id: assignManager.id,
      text: assignManager.name,
      name: assignManager.name,
      value: assignManager.id,
    }));
};
