import React, { useEffect, useRef } from "react";
import jexcel from "jexcel";
import "jexcel/dist/jexcel.css";
import { useDispatch } from "react-redux";
import { useState } from "react";

export default function SpreadSheet({
  options, // jexcel 
  data, // jexcel 
  setSpreadSheet = null, // SpreadSheet  useState  setter
  onReady = null, // jexcel 
}) {
  const [isReady, setIsReady] = useState(false);
  const dispatch = useDispatch();
  const jexcelRef = useRef(null);

  const initialOptions = useRef({
    allowComments: false,
    allowInsertRow: true,
    allowManualInsertRow: false,
    allowManualInsertColumn: false,
    defaultColAlign: "left",
    contextMenu: () => [],
  });

  useEffect(() => {
    const opt = { ...initialOptions.current, ...options.current };
    if (setSpreadSheet) {
      setSpreadSheet(jexcel(jexcelRef.current, opt));
    } else {
      jexcel(jexcelRef.current, opt);
    }
  }, [dispatch, options, setSpreadSheet]);

  useEffect(() => {
    jexcelRef.current.jexcel.setData(data);
  }, [data]);

  // jexcel 
  if (jexcelRef.current && isReady === false) {
    if (onReady) {
      onReady(jexcelRef.current.jexcel);
    }
    setIsReady(true);
  }

  return <div ref={jexcelRef} />;
}
