import lo from "lodash";
import firebase from "../config/firebase";
import { addOrUpdateLastUpdateToFirestore } from "./lastupdateService";

const db = firebase.firestore();
const COLLECTION_NAME = "assign";

export function fetchAssignsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function addAssignToFirestore(assign) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    assign.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(assign.id)
    .set({
      ...assign,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateAssignToFirestore(assign) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    assign.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(assign.id)
    .update({
      ...lo.omit(assign, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}

export function removeAssignToFirestore(assignId) {
  db.collection(COLLECTION_NAME).doc(assignId).delete();
}
