import React, { useEffect, useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import YearMonthTooptip from "./tooltip/YearMonthTooptip";
import WorkinRatioTooltip from "./tooltip/WorkinRatioTooltip";
import SpreadSheet from "../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";
import {
  COL_MEMBER,
  COL_PROJECT,
  COL_WORKING_RATIO,
  COL_WORKING_RATIO_SHOW,
  COL_YEAR_MONTH_SHOW,
  addRowForSpreadSheet,
  editRowForSpreadSheet,
  removeRowForSpreadSheet,
} from "./assignListBehavior";

export default function AssignPage({ location }) {
  const command = location.hash?.replace("#", "");
  const history = useHistory();
  const [tooltipVisible1, setTooltipVisible1] = useState(false);
  const [tooltipVisible2, setTooltipVisible2] = useState(false);
  const dispatch = useDispatch();
  const { currentPeriod } = useSelector((state) => state.period);
  const { members } = useSelector((state) => state.member);
  const { memberTags } = useSelector((state) => state.memberTag);
  const { memberAffiliations } = useSelector(
    (state) => state.memberAffiliation
  );
  const { memberAssignManagers } = useSelector(
    (state) => state.memberAssignManager
  );
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { projects } = useSelector((state) => state.project);
  const { clients } = useSelector((state) => state.client);
  const { accDepartments } = useSelector((state) => state.accDepartment);
  const { accSubjects } = useSelector((state) => state.accSubject);
  const { precisions } = useSelector((state) => state.precision);
  const { assigns } = useSelector((state) => state.assign);
  const [spreadSheet, setSpreadSheet] = useState(null);

  useEffect(() => {
    if (command?.startsWith("add")) {
      // 
      addRowForSpreadSheet(spreadSheet, currentPeriod.yearMonth, dispatch);
      history.push("/assign");
    } else if (command?.startsWith("remove")) {
      // 
      if (window.confirm("") === true) {
        removeRowForSpreadSheet(spreadSheet, dispatch);
      }
      history.push("/assign");
    } else if (command?.startsWith("show" + COL_YEAR_MONTH_SHOW)) {
      // 
      setTooltipVisible1(true);
    } else if (command?.startsWith("show" + COL_WORKING_RATIO_SHOW)) {
      // 
      setTooltipVisible2(true);
    } else if (command.length === 0) {
      // 
      // 
      setTooltipVisible1(false);
      setTooltipVisible2(false);
    }
  }, [command, currentPeriod, dispatch, history, spreadSheet]);

  const handleChange = (instance, cell, x, y, value) => {
    // console.log(instance);
    // console.log(cell);
    // console.log(instance.jexcel);
    // console.log("col(x): " + x);
    // console.log("row(y)" + y);
    // console.log("value:" + value);
    editRowForSpreadSheet(
      instance.jexcel,
      parseInt(x),
      parseInt(y),
      {
        members: members,
        memberTags: memberTags,
        projects: projects,
        accSubjects: accSubjects,
        precisions: precisions,
      },
      dispatch
    );
  };

  const handleReady = (jexcelRef) => {
    // 
    for (let index = 0; index < assigns.length; index++) {
      editRowForSpreadSheet(jexcelRef, parseInt(COL_MEMBER), parseInt(index), {
        members: members,
        memberTags: memberTags,
        projects: projects,
        accSubjects: accSubjects,
        precisions: precisions,
      });
      editRowForSpreadSheet(jexcelRef, parseInt(COL_PROJECT), parseInt(index), {
        members: members,
        memberTags: memberTags,
        projects: projects,
        accSubjects: accSubjects,
        precisions: precisions,
      });
      editRowForSpreadSheet(
        jexcelRef,
        parseInt(COL_WORKING_RATIO),
        parseInt(index),
        {
          members: members,
          memberTags: memberTags,
          projects: projects,
          accSubjects: accSubjects,
          precisions: precisions,
        }
      );
    }
  };

  const options = useRef({
    columns: columnsOptions(
      members,
      memberTags,
      memberAffiliations,
      memberAssignManagers,
      projects,
      clients,
      accDepartments,
      accSubjects,
      memberRanks,
      precisions
    ),
    onchange: handleChange,
  });

  return (
    <>
      <SpreadSheet
        options={options}
        data={assigns}
        setSpreadSheet={setSpreadSheet}
        onReady={handleReady}
      />
      <YearMonthTooptip visible={tooltipVisible1} />
      <WorkinRatioTooltip visible={tooltipVisible2} />
    </>
  );
}
