import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberAssignManagerToFirestore,
  fetchMemberAssignManagersFromFirestore,
  updateMemberAssignManagerToFirestore,
} from "../../../app/firestore/memberAssignManagerService";
import { mustRefresh } from "../../lastupdate/lastupdateReducer";

const MEMBER_ASSIGN_MANAGER_CREATE = "memberAssignManager/create";
const MEMBER_ASSIGN_MANAGER_UPDATE = "memberAssignManager/update";
const MEMBER_ASSIGN_MANAGERS_FETCH = "memberAssignManager/fetch";
const MEMBER_ASSIGN_MANAGER_LISTEN = "memberAssignManager/listen";
const MEMBER_ASSIGN_MANAGER_SELECTED = "memberAssignManager/selected";

export function selectMemberAssignManager(memberAssignManager) {
  return {
    type: MEMBER_ASSIGN_MANAGER_SELECTED,
    payload: memberAssignManager,
  };
}

export function createMemberAssignManager(memberAssignManager) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberAssignManagerToFirestore(memberAssignManager);
      dispatch({
        type: MEMBER_ASSIGN_MANAGER_CREATE,
        payload: memberAssignManager,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberAssignManager(memberAssignManager) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberAssignManagerToFirestore(memberAssignManager);
      dispatch({
        type: MEMBER_ASSIGN_MANAGER_UPDATE,
        payload: memberAssignManager,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberAssignManagers(yyyyMM) {
  return async function (dispatch, getState) {
    try {
      // 
      const must = mustRefresh(
        getState().memberAssignManager.memberAssignManagers,
        getState().lastupdate.member_assign_manager
      );
      if (!must.result) {
        // 
        return getState().memberAssignManager.memberAssignManagers;
      }
      dispatch(asyncActionStart());
      const snapshot = fetchMemberAssignManagersFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}"></a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBER_ASSIGN_MANAGERS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberAssignManagers: [],
  selectedMemberAssignManager: null,
};

export default function memberAssignManagerReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBER_ASSIGN_MANAGER_SELECTED:
      return {
        ...state,
        selectedMemberAssignManager: payload,
      };
    case MEMBER_ASSIGN_MANAGER_CREATE:
      return state;
    case MEMBER_ASSIGN_MANAGER_UPDATE:
      return state;
    case MEMBER_ASSIGN_MANAGERS_FETCH:
      return {
        ...state,
        memberAssignManagers: payload,
      };
    case MEMBER_ASSIGN_MANAGER_LISTEN:
      return {
        ...state,
        selectedMemberAssignManager: payload,
      };
    default:
      return state;
  }
}
