import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";
import { addOrUpdateLastUpdateToFirestore } from "./lastupdateService";

const db = firebase.firestore();
const COLLECTION_NAME = "member_assign_manager";

export function fetchMemberAssignManagersFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberAssignManagerFromFirestore(
  memberAssignManagerId
) {
  return db.collection(COLLECTION_NAME).doc(memberAssignManagerId);
}

export function addMemberAssignManagerToFirestore(memberAssignManager) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    memberAssignManager.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(memberAssignManager.closing_year_month + "-" + cuid())
    .set({
      ...memberAssignManager,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberAssignManagerToFirestore(memberAssignManager) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    memberAssignManager.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(memberAssignManager.id)
    .update({
      ...lo.omit(memberAssignManager, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
