import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addPrecisionToFirestore,
  fetchPrecisionsFromFirestore,
  updatePrecisionToFirestore,
} from "../../../app/firestore/precisionService";
import { mustRefresh } from "../../lastupdate/lastupdateReducer";

const PRECISION_CREATE = "precision/create";
const PRECISION_UPDATE = "precision/update";
const PRECISIONS_FETCH = "precision/fetch";
const PRECISION_LISTEN = "precision/listen";
const PRECISION_SELECTED = "precision/selected";

export function selectPrecision(precision) {
  return {
    type: PRECISION_SELECTED,
    payload: precision,
  };
}

export function createPrecision(precision) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addPrecisionToFirestore(precision);
      dispatch({
        type: PRECISION_CREATE,
        payload: precision,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updatePrecision(precision) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updatePrecisionToFirestore(precision);
      dispatch({
        type: PRECISION_UPDATE,
        payload: precision,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchPrecisions(yyyyMM) {
  return async function (dispatch, getState) {
    try {
      // 
      const must = mustRefresh(
        getState().precision.precisions,
        getState().lastupdate.precision
      );
      if (!must.result) {
        // 
        return getState().precision.precisions;
      }
      dispatch(asyncActionStart());
      const snapshot = fetchPrecisionsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}"></a>`;
        return { href, ...ret };
      });
      dispatch({
        type: PRECISIONS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  precisions: [],
  selectedPrecision: null,
};

export default function precisionReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case PRECISION_SELECTED:
      return {
        ...state,
        selectedPrecision: payload,
      };
    case PRECISION_CREATE:
      return state;
    case PRECISION_UPDATE:
      return state;
    case PRECISIONS_FETCH:
      return {
        ...state,
        precisions: payload,
      };
    case PRECISION_LISTEN:
      return {
        ...state,
        selectedPrecision: payload,
      };
    default:
      return state;
  }
}
