import React, { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { Icon, Menu, Sidebar } from "semantic-ui-react";
import NowLoading from "../../app/common/utils/NowLoading";
import AssignList from "./AssignList";
import { fetchAccDepartments } from "../master/accDepartments/accDepartmentReducer";
import { fetchAccSubjects } from "../master/accSubjects/accSubjectReducer";
import { fetchClients } from "../master/clients/clientReducer";
import { fetchMemberAffiliations } from "../master/memberAffiliations/memberAffiliationReducer";
import { fetchMemberAssignManagers } from "../master/memberAssignManagers/memberAssignManagerReducer";
import { fetchMemberRanks } from "../master/memberRanks/memberRankReducer";
import { fetchMembers } from "../master/members/memberReducer";
import { fetchMemberTags } from "../master/memberTags/memberTagReducer";
import { fetchPrecisions } from "../master/precisions/precisionReducer";
import { fetchProjects } from "../master/projects/projectReducer";
import { fetchAssigns } from "./assignReducer";
import { fetchLastUpdates } from "../lastupdate/lastupdateReducer";

export default function AssignDashboard({ location }) {
  const dispatch = useDispatch();
  const [refresh, setRefresh] = useState(0);
  const { currentPeriod } = useSelector((state) => state.period);
  const { loading, error } = useSelector((state) => state.async);

  useEffect(() => {
    dispatch(fetchLastUpdates(currentPeriod.yearMonth)).then(async () => {
      await Promise.all([
        dispatch(fetchMembers(currentPeriod.yearMonth)),
        dispatch(fetchMemberTags(currentPeriod.yearMonth)),
        dispatch(fetchMemberAffiliations(currentPeriod.yearMonth)),
        dispatch(fetchMemberAssignManagers(currentPeriod.yearMonth)),
        dispatch(fetchMemberRanks(currentPeriod.yearMonth)),
        dispatch(fetchProjects(currentPeriod.yearMonth)),
        dispatch(fetchAccDepartments(currentPeriod.yearMonth)),
        dispatch(fetchAccSubjects(currentPeriod.yearMonth)),
        dispatch(fetchPrecisions(currentPeriod.yearMonth)),
        dispatch(fetchClients(currentPeriod.yearMonth)),
      ]).then(() => {
        dispatch(fetchAssigns(currentPeriod.yearMonth));
      });
    });
  }, [currentPeriod.yearMonth, dispatch, refresh]);

  const handleRefresh = () => {
    setRefresh(refresh + 1);
  };

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar.Pusher className="jpusher">
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>Assign</Menu.Item>
            <Menu.Item
              onClick={() => {
                handleRefresh();
              }}>
              <Icon name="refresh" />
              
            </Menu.Item>
          </Menu>
          <AssignList refresh={refresh} location={location} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
