import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "last_update";

export function fetchLastUpdatesFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM);
}

export function addOrUpdateLastUpdateToFirestore(
  collection_name,
  yyyyMM,
  lastupdate
) {
  if (!lastupdate) {
    return null;
  }
  return db
    .collection(COLLECTION_NAME)
    .doc(yyyyMM + "-" + collection_name)
    .set({
      closing_year_month: yyyyMM,
      lastupdate: lastupdate,
    });
}
