import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberFromFirestore } from "../../../app/firestore/memberService";
import { createMember, selectMember, updateMember } from "./memberReducer";
import { memberHubOptionsFactory } from "../../../app/options/memberHubOptionsFactory";
import { memberRankOptionsFactory } from "../../../app/options/memberRankOptionsFactory";
import { memberAffiliationOptionsFactory } from "../../../app/options/memberAffiliationOptionsFactory";
import { memberTagOptionsFactory } from "../../../app/options/memberTagOptionsFactory";
import { getCostAndSales } from "../memberHubCostSaless/memberHubCostSalesReducer";
import { memberAssignManagerOptionsFactory } from "../../../app/options/memberAssignManagerOptionsFactory";

export default function MemberForm({
  memberId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { memberAffiliations } = useSelector(
    (state) => state.memberAffiliation
  );
  const { memberHubs } = useSelector((state) => state.memberHub);
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { memberAssignManagers } = useSelector(
    (state) => state.memberAssignManager
  );
  const { memberHubCostSaless } = useSelector(
    (state) => state.memberHubCostSales
  );
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMember } = useSelector((state) => state.member);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMember ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    tag: "",
    affiliation: "",
    hub: "",
    rank: "",
    cost: 0,
    sales: 0,
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required(""),
    tag: Yup.string().required(""),
    affiliation: Yup.string().required(""),
    hub: Yup.string().notRequired(),
    rank: Yup.string().notRequired(),
    assign_manager: Yup.string().notRequired(),
    cost: Yup.number().required(""),
    sales: Yup.number().required(""),
    available: Yup.string().required(""),
  });

  useFirestoreDoc({
    shouldExecute: !!memberId,
    query: () => loadFromMemberFromFirestore(memberId),
    data: (item) => dispatch(selectMember(item)),
    deps: [memberId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}></Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberId
              ? dispatch(updateMember(values))
              : dispatch(createMember(values));
            setVisibleDetail(false);
            history.push("/members");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({
          isSubmitting,
          dirty,
          isValid,
          resetForm,
          setFieldValue,
          values,
        }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="" name="name" placeholder="" />
            <MySelectInput
              label=""
              name="tag"
              options={memberTagOptionsFactory(memberTags)}
            />
            <MySelectInput
              label=""
              name="affiliation"
              options={memberAffiliationOptionsFactory(memberAffiliations)}
              search
            />
            <MySelectInput
              label=""
              name="hub"
              options={memberHubOptionsFactory(memberHubs)}
              clearable
              onChange={(e) => {
                // onChangeFormik
                // e.target.id ID
                setFieldValue("hub", e.target.id);
                // 
                const { cost, sales } = getCostAndSales(
                  memberHubCostSaless,
                  e.target.id,
                  values.rank
                );
                setFieldValue("cost", cost);
                setFieldValue("sales", sales);
              }}
            />
            <MySelectInput
              label=""
              name="rank"
              options={memberRankOptionsFactory(memberRanks)}
              clearable
              onChange={(e) => {
                // onChangeFormik
                // e.target.id ID
                setFieldValue("rank", e.target.id);
                // 
                const { cost, sales } = getCostAndSales(
                  memberHubCostSaless,
                  values.hub,
                  e.target.id
                );
                setFieldValue("cost", cost);
                setFieldValue("sales", sales);
              }}
            />
            <MySelectInput
              label=""
              name="assign_manager"
              options={memberAssignManagerOptionsFactory(memberAssignManagers)}
              search
            />
            <MyTextInput label="" name="cost" placeholder="" />
            <MyTextInput label="" name="sales" placeholder="" />
            <MySelectInput
              label=""
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content=""
            />
            <Button
              negative
              content=""
              as={Link}
              to="/members"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
