import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../app/firestore/firestoreService";
import {
  addOrUpdateLastUpdateToFirestore,
  fetchLastUpdatesFromFirestore,
} from "../../app/firestore/lastupdateService";

const LASTUPDATE_FETCH = "lastupdate/fetch";
const LASTUPDATE_UPDATE = "lastupdate/update";

export function fetchLastUpdates(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchLastUpdatesFromFirestore(yyyyMM).get();
      const data = {
        acc_department: null,
        acc_subject: null,
        assign: null,
        client: null,
        member: null,
        member_affiliation: null,
        member_assign_manager: null,
        member_hub: null,
        member_hub_cost_sales: null,
        member_rank: null,
        member_tag: null,
        precision: null,
        project: null,
      };
      (await snapshot).docs.forEach((doc) => {
        const item = dataFromSnapshot(doc);
        const collection_name = item.id.slice(7);
        data[collection_name] = item.lastupdate;
      });
      dispatch({
        type: LASTUPDATE_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateLastUpdates(collection_name, yyyyMM, lastupdate) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addOrUpdateLastUpdateToFirestore(
        collection_name,
        yyyyMM,
        lastupdate
      );
      dispatch({
        type: LASTUPDATE_UPDATE,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

/**
 * 
 * @param {array} current 
 * @param {*} lastupdate 
 */
export function mustRefresh(current, lastupdate) {
  if (Array.isArray(current) && current.length > 0 && lastupdate) {
    let last = null;
    current.forEach((item) => {
      // 
      if (!last || item.updated_at.getTime() > last.getTime()) {
        last = item.updated_at;
      }
    });
    // 
    console.log(last.getTime(), lastupdate.getTime());
    console.log(
      last.getTime() === lastupdate.getTime() ? "not refresh" : "refresh"
    );
    return last.getTime() === lastupdate.getTime()
      ? { result: false, lastupdate: null }
      : { result: true, lastupdate: last };
  }
  // 
  console.log("refresh");
  return { result: true, lastupadte: null };
}

export default function lastUpdateReducer(state = {}, { type, payload }) {
  switch (type) {
    case LASTUPDATE_FETCH:
      return payload;
    case LASTUPDATE_UPDATE:
      return state;
    default:
      return state;
  }
}
