import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function PrecisionList({ refresh }) {
  const { precisions } = useSelector((state) => state.precision);

  const options = useRef({
    columns: columnsOptions(),
  });

  return <SpreadSheet options={options} data={precisions} />;
}
