import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberAssignManagerFromFirestore } from "../../../app/firestore/memberAssignManagerService";
import {
  createMemberAssignManager,
  selectMemberAssignManager,
  updateMemberAssignManager,
} from "./memberAssignManagerReducer";

export default function MemberAssignManagerForm({
  memberAssignManagerId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMemberAssignManager } = useSelector(
    (state) => state.memberAssignManager
  );
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMemberAssignManager ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required(""),
    available: Yup.string().required(""),
  });

  useFirestoreDoc({
    shouldExecute: !!memberAssignManagerId,
    query: () =>
      loadFromMemberAssignManagerFromFirestore(memberAssignManagerId),
    data: (item) => dispatch(selectMemberAssignManager(item)),
    deps: [memberAssignManagerId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>
          
        </Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberAssignManagerId
              ? dispatch(updateMemberAssignManager(values))
              : dispatch(createMemberAssignManager(values));
            setVisibleDetail(false);
            history.push("/memberassignmanagers");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput
              label=""
              name="name"
              placeholder=""
            />
            <MySelectInput
              label=""
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content=""
            />
            <Button
              negative
              content=""
              as={Link}
              to="/memberassignmanagers"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
