import React from "react";
import MouseTooltip from "react-sticky-mouse-tooltip";
import { Header, Segment, Table } from "semantic-ui-react";

export default function YearMonthTooptip({ offsetX, offsetY, visible }) {
  return (
    <MouseTooltip offsetX={15} offsetY={-30} visible={visible}>
      <Segment.Group>
        <Segment>
          <Header as="h4"></Header>
        </Segment>
        <Segment>
          <Table compact="very" size="small">
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell></Table.HeaderCell>
                <Table.HeaderCell></Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell></Table.Cell>
                <Table.Cell>20%</Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>NIPPO</Table.Cell>
                <Table.Cell>80%</Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
        </Segment>
      </Segment.Group>
    </MouseTooltip>
  );
}
