import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../app/async/asyncReducer";
import { fetchAssignsFromFirestore } from "../../app/firestore/assignService";
import { dataFromSnapshot } from "../../app/firestore/firestoreService";
import { mustRefresh } from "../lastupdate/lastupdateReducer";
import {
  createShowYearMonthTag,
  createShowWorkingRatioTag,
  createAddTag,
  createRemoveTag,
} from "./assignListBehavior";

const ASSIGN_ADD_ROW = "assign/addRow";
const ASSIGN_REMOVE_ROW = "assign/removeRow";
const ASSIGN_EDIT_ROW = "assign/editRow";
const ASSIGN_FETCH = "assign/fetch";

export function addAssign(assign) {
  return {
    type: ASSIGN_ADD_ROW,
    payload: assign,
  };
}

export function removeAssign(assignId) {
  return {
    type: ASSIGN_REMOVE_ROW,
    payload: assignId,
  };
}

export function editAssign(assign) {
  return {
    type: ASSIGN_EDIT_ROW,
    payload: assign,
  };
}

export function fetchAssigns(yyyyMM) {
  return async function (dispatch, getState) {
    try {
      // 
      const must = mustRefresh(
        getState().assign.assigns,
        getState().lastupdate.assign
      );
      if (!must.result) {
        // 
        return getState().assign.assigns;
      }
      dispatch(asyncActionStart());
      const snapshot = fetchAssignsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        return {
          add_row: createAddTag(),
          remove_row: createRemoveTag(),
          year_month_show: createShowYearMonthTag(ret.member, ret.year_month),
          working_ratio_show: createShowWorkingRatioTag(ret.member),
          ...ret,
        };
      });
      dispatch({
        type: ASSIGN_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

/**
 * Assign
 * @param {*} src
 * @param {*} dst
 */
function equalAssign(src, dst) {
  // 
  if (
    src.id === dst.id &&
    src.member === dst.member &&
    src.year_month === dst.year_month &&
    src.budget_performance_class === dst.budget_performance_class &&
    src.member_assign_manager === dst.member_assign_manager &&
    src.project === dst.project &&
    src.sales === dst.sales &&
    src.cost === dst.cost &&
    src.working_ratio === dst.working_ratio &&
    src.precision === dst.precision &&
    src.closing_year_month === dst.closing_year_month
  ) {
    return true;
  } else {
    return false;
  }
}

const initialState = {
  assigns: [],
  add: [],
  update: [],
  remove: [],
};

export default function assignReducer(state = initialState, { type, payload }) {
  switch (type) {
    case ASSIGN_ADD_ROW:
      // TODO add, assigns 
      state.add.push(payload);
      return state;
    case ASSIGN_REMOVE_ROW:
      // TODO remove, assigns
      state.remove.push(payload);
      return state;
    case ASSIGN_EDIT_ROW:
      // TODO , editassigns
      const assign = state.assigns.filter((item) => item.id === payload.id);
      if (assign.length > 0) {
        if (!equalAssign(assign[0], payload)) {
          state.update.push(payload);
        }
      }
      return state;
    case ASSIGN_FETCH:
      return {
        ...state,
        assigns: payload,
      };
    default:
      return state;
  }
}
