import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";
import { addOrUpdateLastUpdateToFirestore } from "./lastupdateService";

const db = firebase.firestore();
const COLLECTION_NAME = "precision";

export function fetchPrecisionsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromPrecisionFromFirestore(precisionId) {
  return db.collection(COLLECTION_NAME).doc(precisionId);
}

export function addPrecisionToFirestore(precision) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    precision.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(precision.closing_year_month + "-" + cuid())
    .set({
      ...precision,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updatePrecisionToFirestore(precision) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.Timestamp.now();
  addOrUpdateLastUpdateToFirestore(
    COLLECTION_NAME,
    precision.closing_year_month,
    now
  );
  return db
    .collection(COLLECTION_NAME)
    .doc(precision.id)
    .update({
      ...lo.omit(precision, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
