import cuid from "cuid";
import { addAssign, editAssign, removeAssign } from "./assignReducer";

/** Err/War */
export const COL_ERRORS = 2;
/**  */
export const COL_MEMBER = 3;
/** /PT */
export const COL_MEMBER_TAG = 4;
/**  */
export const COL_MEMBER_AFFILIATION = 5;
/**  */
export const COL_YEAR_MONTH = 6;
/** () */
export const COL_YEAR_MONTH_SHOW = 7;
/** // */
export const COL_BUDGET_PERFORMANCE_CLASS = 8;
/**  */
export const COL_MEMBER_ASSIGN_MANAGER = 9;
/**  */
export const COL_PROJECT = 10;
/**  */
export const COL_CLIENT = 11;
/**  */
export const COL_ACC_DEPARTMENT = 12;
/**  */
export const COL_ACC_SUBJECT = 13;
/**  */
export const COL_SALES = 14;
/** /PT */
export const COL_COST = 15;
/**  */
export const COL_WORKING_RATIO = 16;
/** () */
export const COL_WORKING_RATIO_SHOW = 17;
/**  */
export const COL_INHOUSE_COST = 18;
/**  */
export const COL_PROFIT = 19;
/**  */
export const COL_PROFIT_RATIO = 20;
/**  */
export const COL_MEMBER_RANK = 21;
/**  */
export const COL_MEMBER_SALES = 22;
/**  */
export const COL_MEMBER_SALES_DIFF = 23;
/**  */
export const COL_PRECISION = 24;
/** ID */
export const COL_ID = 25;
/**  */
export const COL_CLOSING_YEAR_MONTH = 26;

/**
 * 
 * @param {*} spreadSheetRef 
 * @param {string} closing_year_month 
 */
export function addRowForSpreadSheet(
  spreadSheetRef,
  closing_year_month,
  dispatch = null
) {
  if (!spreadSheetRef) {
    return;
  }
  const currentRowIds = spreadSheetRef.getSelectedRows(true);
  if (currentRowIds && currentRowIds.length > 0) {
    // SpreadSheet
    const newRowData = duplicateRowData(
      spreadSheetRef,
      currentRowIds[0],
      closing_year_month
    );
    spreadSheetRef.insertRow(newRowData, currentRowIds[0]);
    // 
    const assign = {
      id: newRowData[COL_ID],
      closing_year_month: newRowData[COL_CLOSING_YEAR_MONTH],
      member: newRowData[COL_MEMBER],
      year_month: newRowData[COL_YEAR_MONTH],
      budget_performance_class: newRowData[COL_BUDGET_PERFORMANCE_CLASS],
      member_assign_manager: newRowData[COL_MEMBER_ASSIGN_MANAGER],
      project: newRowData[COL_PROJECT],
      sales: _stripCamma(newRowData[COL_SALES]),
      cost: _stripCamma(newRowData[COL_COST]),
      working_ratio: _stripToZero(newRowData[COL_WORKING_RATIO]),
      precision: newRowData[COL_PRECISION],
    };
    if (dispatch) {
      dispatch(addAssign(assign));
    }
  }
}

/**
 * 
 * @param {*} spreadSheetRef 
 * @param {number} srcRowId 
 * @param {string} closing_year_month 
 */
function duplicateRowData(spreadSheetRef, srcRowId, closing_year_month) {
  const currentRowData = spreadSheetRef.getRowData(srcRowId);
  currentRowData[COL_YEAR_MONTH_SHOW] = createShowYearMonthTag(
    currentRowData[COL_MEMBER],
    currentRowData[COL_YEAR_MONTH]
  );
  currentRowData[COL_ERRORS] = "";
  currentRowData[COL_PROJECT] = "";
  currentRowData[COL_CLIENT] = "";
  currentRowData[COL_ACC_DEPARTMENT] = "";
  currentRowData[COL_ACC_SUBJECT] = "";
  currentRowData[COL_SALES] = "0";
  currentRowData[COL_WORKING_RATIO] = "0 %";
  currentRowData[COL_WORKING_RATIO_SHOW] = createShowWorkingRatioTag(
    currentRowData[COL_MEMBER]
  );
  currentRowData[COL_PRECISION] = "";
  currentRowData[COL_ID] = createId(closing_year_month);
  currentRowData[COL_CLOSING_YEAR_MONTH] = closing_year_month;
  return currentRowData;
}

/**
 * ID
 * @param {string}} closing_year_month 
 */
function createId(closing_year_month) {
  return closing_year_month + "-" + cuid();
}

/**
 * 
 */
export function createAddTag() {
  return `<button class="ui positive mini button" onclick='location.href="#add"'></button>`;
}

/**
 * 
 */
export function createRemoveTag() {
  return `<button class="ui negative mini button" onclick='location.href="#remove"'></button>`;
}

/**
 * 
 * @param {string} targetMemberId ID
 * @param {*} targetYearMonth 
 */
export function createShowYearMonthTag(targetMemberId, targetYearMonth) {
  return `<a href='#' onmouseover='location.href="#show${COL_YEAR_MONTH_SHOW}=${targetMemberId}&ym=${targetYearMonth}"' onmouseout='location.href="#"'></a>`;
}

/**
 * 
 * @param {string} targetMemberId ID
 */
export function createShowWorkingRatioTag(targetMemberId) {
  return `<a href='#' onmouseover='location.href="#show${COL_WORKING_RATIO_SHOW}=${targetMemberId}"' onmouseout='location.href="#"'></a>`;
}

/**
 * 
 * @param {*} spreadSheetRef 
 */
export function removeRowForSpreadSheet(spreadSheetRef, dispatch = null) {
  if (!spreadSheetRef) {
    return;
  }
  const currentRowIds = spreadSheetRef.getSelectedRows(true);
  if (currentRowIds && currentRowIds.length > 0) {
    // SpreadSheet
    spreadSheetRef.deleteRow(currentRowIds[0], 1);
    // 
    const currentRowData = spreadSheetRef.getRowData(currentRowIds[0]);
    if (dispatch) {
      // TODO currentRowData[COL_ID] 
      dispatch(removeAssign(currentRowData[COL_ID]));
    }
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} col  Col 
 * @param {number} row  Row 
 * @param {*} master 
 */
export function editRowForSpreadSheet(
  spreadSheetRef,
  col,
  row,
  master = {},
  dispatch = null
) {
  // SpreadSheet
  inputForSpreadSheet(spreadSheetRef, col, row, master);
  // 
  const currentRowData = spreadSheetRef.getRowData(row);
  const assign = {
    id: currentRowData[COL_ID],
    closing_year_month: currentRowData[COL_CLOSING_YEAR_MONTH],
    member: currentRowData[COL_MEMBER],
    year_month: currentRowData[COL_YEAR_MONTH],
    budget_performance_class: currentRowData[COL_BUDGET_PERFORMANCE_CLASS],
    member_assign_manager: currentRowData[COL_MEMBER_ASSIGN_MANAGER],
    project: currentRowData[COL_PROJECT],
    sales: _stripCamma(currentRowData[COL_SALES]),
    cost: _stripCamma(currentRowData[COL_COST]),
    working_ratio: currentRowData[COL_WORKING_RATIO],
    precision: currentRowData[COL_PRECISION],
  };
  if (dispatch) {
    dispatch(editAssign(assign));
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} col  Col 
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForSpreadSheet(spreadSheetRef, col, row, masters = {}) {
  if (col === COL_MEMBER) {
    inputForColMember(spreadSheetRef, row, masters);
  } else if (col === COL_MEMBER_TAG) {
    inputForColMemberTag(spreadSheetRef, row, masters);
  } else if (col === COL_PROJECT) {
    inputForColProject(spreadSheetRef, row, masters);
  } else if (col === COL_ACC_SUBJECT) {
    inputForColAccSubject(spreadSheetRef, row, masters);
  } else if (col === COL_SALES) {
    inputForColSales(spreadSheetRef, row);
  } else if (col === COL_COST) {
    inputForColCost(spreadSheetRef, row);
  } else if (col === COL_WORKING_RATIO) {
    inputForColWorkingRatio(spreadSheetRef, row);
  } else if (col === COL_PRECISION) {
    inputForColPrecision(spreadSheetRef, row, masters);
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForColMember(spreadSheetRef, row, masters = {}) {
  const memberId = spreadSheetRef.getValueFromCoords(COL_MEMBER, row);
  const member = masters.members.filter((item) => {
    return item.id === memberId;
  })[0];

  if (member) {
    // /PT
    spreadSheetRef.setValueFromCoords(COL_MEMBER_TAG, row, member.tag, true);
    // 
    spreadSheetRef.setValueFromCoords(
      COL_MEMBER_AFFILIATION,
      row,
      member.affiliation,
      true
    );
    // 
    spreadSheetRef.setValueFromCoords(
      COL_MEMBER_ASSIGN_MANAGER,
      row,
      member.assign_manager,
      true
    );
    // /PT
    spreadSheetRef.setValueFromCoords(COL_COST, row, member.cost, true);
    // 
    spreadSheetRef.setValueFromCoords(COL_MEMBER_RANK, row, member.rank, true);
    // 
    spreadSheetRef.setValueFromCoords(
      COL_MEMBER_SALES,
      row,
      member.sales,
      true
    );
    // /PT
    inputForColCost(spreadSheetRef, row);
    // 
    inputForColMemberSales(spreadSheetRef, row);
  } else {
    // /PT
    spreadSheetRef.setValueFromCoords(COL_MEMBER_TAG, row, "", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_MEMBER_AFFILIATION, row, "", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_MEMBER_ASSIGN_MANAGER, row, "", true);
    // /PT
    spreadSheetRef.setValueFromCoords(COL_COST, row, "0", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_MEMBER_RANK, row, "", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_MEMBER_SALES, row, "0", true);
    // /PT
    inputForColCost(spreadSheetRef, row);
    // 
    inputForColMemberSales(spreadSheetRef, row);
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForColMemberTag(spreadSheetRef, row, masters = {}) {
  const memberTagId = spreadSheetRef.getValueFromCoords(COL_MEMBER_TAG, row);
  const memberTag = masters.memberTags.filter((item) => {
    return item.id === memberTagId;
  })[0];

  if (memberTag && memberTag.use_cost_sales.toLowerCase() === "true") {
    spreadSheetRef.getCellFromCoords(COL_COST, row).classList.add("readonly");
  } else {
    // 
    // 
    spreadSheetRef
      .getCellFromCoords(COL_COST, row)
      .classList.remove("readonly");
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForColProject(spreadSheetRef, row, masters = {}) {
  const projectId = spreadSheetRef.getValueFromCoords(COL_PROJECT, row);
  const project = masters.projects.filter((item) => {
    return item.id === projectId;
  })[0];

  if (project) {
    // 
    spreadSheetRef.setValueFromCoords(COL_CLIENT, row, project.client, true);
    // 
    spreadSheetRef.setValueFromCoords(
      COL_ACC_DEPARTMENT,
      row,
      project.accDepartment,
      true
    );
    // 
    spreadSheetRef.setValueFromCoords(
      COL_ACC_SUBJECT,
      row,
      project.accSubject,
      true
    );
  } else {
    // 
    spreadSheetRef.setValueFromCoords(COL_CLIENT, row, "", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_ACC_DEPARTMENT, row, "", true);
    // 
    spreadSheetRef.setValueFromCoords(COL_ACC_SUBJECT, row, "", true);
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForColAccSubject(spreadSheetRef, row, masters = {}) {
  const accSubjectId = spreadSheetRef.getValueFromCoords(COL_ACC_SUBJECT, row);
  const accSubject = masters.accSubjects.filter((item) => {
    return item.id === accSubjectId;
  })[0];

  const precisionId = spreadSheetRef.getValueFromCoords(COL_PRECISION, row);
  const precision = masters.precisions.filter((item) => {
    return item.id === precisionId;
  })[0];

  if (!accSubject) {
    // Nothing.
  } else if (accSubject.can_input_sales.toLowerCase() === "true") {
    spreadSheetRef
      .getCellFromCoords(COL_SALES, row)
      .classList.remove("readonly");
  } else {
    // 
    // 
    spreadSheetRef.getCellFromCoords(COL_SALES, row).classList.add("readonly");
    spreadSheetRef.setValueFromCoords(COL_SALES, row, "0", true);
  }
  // 
  if (!precision || !accSubject) {
    // Nothing
  } else if (
    precision.can_input_sales.toLowerCase() !==
    accSubject.can_input_sales.toLowerCase()
  ) {
    spreadSheetRef.setValueFromCoords(COL_PRECISION, row, "", true);
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 * @param {*} masters
 */
function inputForColPrecision(spreadSheetRef, row, masters = {}) {
  const accSubjectId = spreadSheetRef.getValueFromCoords(COL_ACC_SUBJECT, row);
  const accSubject = masters.accSubjects.filter((item) => {
    return item.id === accSubjectId;
  })[0];

  const precisionId = spreadSheetRef.getValueFromCoords(COL_PRECISION, row);
  const precision = masters.precisions.filter((item) => {
    return item.id === precisionId;
  })[0];

  if (!precision) {
    // Nothing.
  } else if (precision.can_input_sales.toLowerCase() === "true") {
    spreadSheetRef
      .getCellFromCoords(COL_SALES, row)
      .classList.remove("readonly");
  } else {
    // 
    // 
    spreadSheetRef.getCellFromCoords(COL_SALES, row).classList.add("readonly");
    spreadSheetRef.setValueFromCoords(COL_SALES, row, "0", true);
  }
  // 
  if (!precision || !accSubject) {
    // Nothing
  } else if (
    accSubject.can_input_sales.toLowerCase() !==
    precision.can_input_sales.toLowerCase()
  ) {
    spreadSheetRef.setValueFromCoords(COL_PROJECT, row, "", true);
  }
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 */
function inputForColSales(spreadSheetRef, row) {
  // /
  const cost = _stripToZero(spreadSheetRef.getValueFromCoords(COL_COST, row));
  const workingRatio = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_WORKING_RATIO, row)
  );
  const salesRaw = spreadSheetRef.getValueFromCoords(COL_SALES, row);
  const sales = _stripToZero(salesRaw);
  const inHouseCost = calcInHouseCost(cost, workingRatio);
  const profit = calcProfit(inHouseCost, sales);
  const profitRatio = calcProfitRatio(profit, sales);
  const memberSales = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_MEMBER_SALES, row)
  );
  const memberSalesDiff = calcMemberSalesDiff(sales, memberSales, workingRatio);

  // 
  spreadSheetRef.setValueFromCoords(COL_PROFIT, row, profit, true);
  // 
  spreadSheetRef.setValueFromCoords(
    COL_PROFIT_RATIO,
    row,
    profitRatio + " %",
    true
  );
  // 
  spreadSheetRef.setValueFromCoords(
    COL_MEMBER_SALES_DIFF,
    row,
    memberSalesDiff,
    true
  );
  // 
  if (!salesRaw) {
    // Delete 
    spreadSheetRef.getCellFromCoords(COL_SALES, row).innerHTML = sales;
  }
}

/**
 * /PT
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 */
function inputForColCost(spreadSheetRef, row) {
  // /
  const cost = _stripToZero(spreadSheetRef.getValueFromCoords(COL_COST, row));
  const workingRatio = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_WORKING_RATIO, row)
  );
  const sales = _stripToZero(spreadSheetRef.getValueFromCoords(COL_SALES, row));
  const inHouseCost = calcInHouseCost(cost, workingRatio);
  const profit = calcProfit(inHouseCost, sales);
  const profitRatio = calcProfitRatio(profit, sales);

  // 
  spreadSheetRef.setValueFromCoords(COL_INHOUSE_COST, row, inHouseCost, true);
  // 
  spreadSheetRef.setValueFromCoords(COL_PROFIT, row, profit, true);
  // 
  spreadSheetRef.setValueFromCoords(
    COL_PROFIT_RATIO,
    row,
    profitRatio + " %",
    true
  );
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 */
function inputForColWorkingRatio(spreadSheetRef, row) {
  // /
  const cost = _stripToZero(spreadSheetRef.getValueFromCoords(COL_COST, row));
  const workingRatio = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_WORKING_RATIO, row)
  );
  const sales = _stripToZero(spreadSheetRef.getValueFromCoords(COL_SALES, row));
  const inHouseCost = calcInHouseCost(cost, workingRatio);
  const profit = calcProfit(inHouseCost, sales);
  const profitRatio = calcProfitRatio(profit, sales);
  const memberSales = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_MEMBER_SALES, row)
  );
  const memberSalesDiff = calcMemberSalesDiff(sales, memberSales, workingRatio);

  // 
  spreadSheetRef.setValueFromCoords(COL_INHOUSE_COST, row, inHouseCost, true);
  // 
  spreadSheetRef.setValueFromCoords(COL_PROFIT, row, profit, true);
  // 
  spreadSheetRef.setValueFromCoords(
    COL_PROFIT_RATIO,
    row,
    profitRatio + " %",
    true
  );
  // 
  spreadSheetRef.setValueFromCoords(
    COL_MEMBER_SALES_DIFF,
    row,
    memberSalesDiff,
    true
  );
  // 
  spreadSheetRef.getCellFromCoords(COL_WORKING_RATIO, row).innerHTML =
    workingRatio + " %";
}

/**
 * 
 * @param {*} spreadSheetRef
 * @param {number} row  Row 
 */
function inputForColMemberSales(spreadSheetRef, row) {
  // /
  const sales = _stripToZero(spreadSheetRef.getValueFromCoords(COL_SALES, row));
  const workingRatio = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_WORKING_RATIO, row)
  );
  const memberSales = _stripToZero(
    spreadSheetRef.getValueFromCoords(COL_MEMBER_SALES, row)
  );
  const memberSalesDiff = calcMemberSalesDiff(sales, memberSales, workingRatio);

  // 
  spreadSheetRef.setValueFromCoords(
    COL_MEMBER_SALES_DIFF,
    row,
    memberSalesDiff,
    true
  );
}

/**
 * 
 * /PT  
 * @param {number} cost /PT
 * @param {number} workingRatio 
 */
function calcInHouseCost(cost, workingRatio) {
  if (workingRatio === 0) {
    return 0;
  }
  return Math.round(cost * (workingRatio / 100));
}

/**
 * 
 *   
 * @param {number} inHouseCost 
 * @param {number} sales 
 */
function calcProfit(inHouseCost, sales) {
  return sales - inHouseCost;
}

/**
 * 
 *   
 * @param {number} profit 
 * @param {number} sales 
 */
function calcProfitRatio(profit, sales) {
  if (sales === 0) {
    return 0;
  }
  return Math.round((profit / sales) * 100 * 10) / 10;
}

/**
 * 
 *     
 * @param {number} sales 
 * @param {number} memberSales 
 * @param {number} workingRatio 
 */
function calcMemberSalesDiff(sales, memberSales, workingRatio) {
  // 
  if (memberSales === 0) {
    return 0;
  }
  if (workingRatio === 0) {
    return sales;
  }
  return Math.round(sales - memberSales * (workingRatio / 100));
}

/**
 * 
 * 
 * @param {*} src
 */
function _stripToZero(src) {
  src = "" + src;
  src = src.replace(/[^0-9.-]/g, "");
  if (src.length === 0) {
    src = 0;
  }
  return parseFloat(src);
}

/**
 * 
 * @param {*} src
 */
function _stripCamma(src) {
  src = "" + src;
  return src.replace(/[,]/g, "");
}
